import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../../../../node_modules/css-loader/dist/runtime/sourceMaps.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "\n.item-count,\n    .selected-filter {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n}\n.items-per-page .items,\n    .filters .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n}\n.items-per-page .items .option,\n    .filters .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n}\n.items-per-page .items .option:hover,\n    .filters .items .option:hover {\n        background-color: #EEEEEE;\n}\n.items-per-page i,\n    .filters i {\n        font-size: 16px;\n        color: #BCBCBC;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/backoffice/user/userList.vue"],"names":[],"mappings":";AA+YI;;QAEI,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;;QAEI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;QAC1C,eAAe;AACnB;AACA;;QAEI,cAAc;QACd,kBAAkB;QAClB,iBAAiB;AACrB;AACA;;QAEI,yBAAyB;AAC7B;AACA;;QAEI,eAAe;QACf,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\n    import { computed, onBeforeMount, reactive, ref, watch, watchEffect } from \"vue\";\n    import { IUser_SUSI } from \"../../../../../../../common/qCloudTemp/authentication\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IRouter } from \"../../../../../domain/presentation/IRouter\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import { IBackofficeUseCase } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import CustomSelect from \"../../customSelect.vue\"\n\n    interface IUser_SUSIData extends IUser_SUSI {\n        organizationNames?: string;\n        showStandAloneInfo?: boolean;\n    }\n    interface IUserListProps {\n        users: Array<IUser_SUSIData>;\n        displayOrgNames?: boolean;\n    }\n    \n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const router = container.resolve<IRouter>(IRouter);\n    const props = defineProps<IUserListProps>();\n    const users = props.users;\n    const searchUser = ref(\"\");\n\n    const currentPage = ref(1);\n    const paginatedData = ref<Array<IUser_SUSIData>>([]);\n    const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n    const selectedItemsPerPage = ref('10');\n    const filterOptions = ref(['All', 'SA Users']);\n    const selectedFilter = ref(\"All\");\n\n\n    onBeforeMount(async () => {\n        if (props.displayOrgNames) {await getUsersOrganizationNames()};\n    })    \n    function viewUser(userId: string){\n        router.push(`/backoffice/user/${userId}`);\n    }\n    // Activity gelene kadar test için yapıldı.\n    // users.forEach((user,index) => {\n    //     if(index < 10) {\n    //         user.activityLevel = 'Inactive';\n    //     }\n    //     else if(index >= 10 && index < 20) {\n    //         user.activityLevel = 'Moderate';\n    //     }\n    //     else if(index >=20 && index < 36 ) {\n    //         user.activityLevel = 'NoneCalculated'\n    //     }\n    //     else {\n    //         user.activityLevel = 'Active'\n    //     }\n    // })\n    ///////////////////////////////////////////////////\n    function getUserList() {\n        const result = users.filter((user: IUser_SUSI) => (user.name?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.lastName?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.email?.toUpperCase().includes(searchUser.value.toUpperCase())));\n        if(selectedFilter.value == 'All') {\n            return result;\n        }\n        else if(selectedFilter.value == 'SA Users') {\n            return result.filter((user: IUser_SUSI) => user.standAloneInfo);\n        }\n\n        return result;\n        \n\n    }\n    async function getUsersOrganizationNames() {\n        await executor.execute(async () => {\n            users.forEach(async user => {\n                user.organizationNames = await backofficeUseCase.getUsersOrganizationNames(user.organizationIds)\n            })\n        },{loading: true})\n    }\n\n    function showStandAloneInfo(user: IUser_SUSIData) {\n        if(user.showStandAloneInfo == undefined) {user.showStandAloneInfo = false;}\n        user.showStandAloneInfo = !user.showStandAloneInfo;\n    }\n        \n    const totalPages = computed(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            return Math.ceil(getUserList().length / parseInt(selectedItemsPerPage.value))\n        }\n        else {\n            return 1;\n        }\n    });\n\n    const displayedPageNumbers = computed(() => {\n        const range = 2; // Number of page numbers to display on each side of the current page\n        let startPage = Math.max(1, currentPage.value - range);\n        let endPage = Math.min(totalPages.value, currentPage.value + range);\n        if (endPage - startPage < 2 * range) {\n            // Adjust page numbers to always show the same number of items\n            if (startPage === 1) {\n                endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n            } \n            else if (endPage === totalPages.value) {\n                startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n            }\n        }\n        return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    });\n\n    watchEffect(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n            const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n            const newData = getUserList().slice(startIndex, endIndex);\n            paginatedData.value = newData;\n        }\n        else {\n            paginatedData.value = getUserList();\n        }\n    });\n\n    function onItemsPerPageSelect (itemCount: string) {\n        selectedItemsPerPage.value = itemCount;\n        setCurrentPage(1)\n    }\n    function setCurrentPage(page: number) {\n        currentPage.value = page;\n    }\n\n    watchEffect(() => {\n        currentPage.value = 1;\n    });\n    function filterTable(filterType: string) {\n        switch(filterType) {\n            case 'All' : selectedFilter.value = \"All\"; break;\n            // case 'Active': selectedFilter.value = 'Active'; break;\n            // case 'Moderate': selectedFilter.value = 'Moderate'; break;\n            // case 'Inactive': selectedFilter.value = 'Inactive'; break;\n            case 'SA Users': selectedFilter.value = \"SA Users\"; break;\n        }\n    }\n    function formatDate(date: Date) {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(date));\n        return formattedDate;\n    }\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span>User List ({{ users.length }})</span>\n    </div>\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchUser\" @input=\"setCurrentPage(1)\"/>\n        </div>\n        <div>\n            <CustomSelect :options=\"filterOptions\" :default=\"filterOptions[0]\" @input=\"filterTable\" class=\"filters\" :selected-class=\"'selected-filter'\"></CustomSelect>\n        </div>\n        <div class=\"quick-filters\">\n            <label>Quick Filters: </label>\n            <!-- <span :class=\"{active: selectedFilter[0] == 4}\" @click=\"filterTable('Active')\">Active</span>\n            <span :class=\"{active: selectedFilter[0] == 1}\" @click=\"filterTable('Moderate')\">Moderate</span>\n            <span :class=\"{active: selectedFilter[0] == 0 && selectedFilter[1] == 0}\" @click=\"filterTable('Non-Active')\">Non-Active</span> -->\n        </div>\n    </div>\n    <div style=\"padding-bottom: 50px;\">\n        <table class=\"users-table\">\n            <tr class=\"thead\">\n                <!-- <th class=\"col-xl-3\">ID</th> -->\n                <!-- <th class=\"col-xl-2\">Object ID</th> -->\n                <th class=\"col-xl-2\">Name</th>\n                <th class=\"col-xl-2\">Last Name</th>\n                <th :class=\"{'col-xl-3': !props.displayOrgNames, 'col-xl-2': props.displayOrgNames }\">E-mail</th>\n                <th class=\"col-xl-2\" v-if=\"props.displayOrgNames\">Organizations</th>\n                <th :class=\"{'col-xl-3': !props.displayOrgNames, 'col-xl-2': props.displayOrgNames }\">Last Login</th>\n                <th class=\"col-xl-1\">SA</th>\n                <th class=\"col-xl-1\">Activity</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"user in paginatedData\" :key=\"user.id\">\n                <td>{{ user.name }}</td>\n                <td>{{ user.lastName }}</td>\n                <td>{{ user.email }}</td>\n                <td v-if=\"props.displayOrgNames\">{{ user.organizationNames }}</td>\n                <td>{{ user.lastSignIn ? formatDate(user.lastSignIn) : '-' }} </td>\n                <td style=\"padding: 18px 36px;\">\n                    <i v-if=\"user.standAloneInfo\" @click=\"showStandAloneInfo(user)\"  class=\"mdi mdi-palette-outline standalone-info\"></i>\n                    <div v-if=\"user.showStandAloneInfo\" class=\"button-tooltip\">\n                        <div><b>Organization Group:</b> {{ user.standAloneInfo?.orgGroup }}</div>\n                        <div><b>App Name:</b> {{ user.standAloneInfo?.appName }}</div>\n                        <div><b>Shell Info:</b> {{ user.standAloneInfo?.shellInfo }}</div>\n                        <div><b>Reason:</b> {{ user.standAloneInfo?.reason}}</div>\n                    </div>\n                </td>\n                <td>  \n                    <!-- <span style=\"background: #FF6B6B; border-radius: 50%;padding: 6px 10px;\">0</span>  -->\n                    <!-- <span style=\"border-radius: 50%;padding: 6px 10px;\" \n                    :style=\"{background: user.activity == 0 ? '#FF6B6B' : user.activity && user.activity >= 1 && user.activity <= 3 ? '#F3CA40' : user.activity && user.activity >=4 ? '#29C660' : ''}\">\n                    {{ user.activity }}</span>  -->\n                </td>\n                <td><button class=\"view-btn\" @click=\"viewUser(user.id)\">VIEW</button></td>\n            </tr>\n            <!-- Pagination -->\n            <tr>\n                <td colspan=\"1\" style=\"display: flex; align-items: center;\">\n                    <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                    <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                </td>\n                <td colspan=\"7\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n\n    </div>\n</template>\n\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n.page-title {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: #151233;\n}\n.filter-area {\n    margin: 20px 0;\n    display: flex;\n    align-items: center;\n}\n.search-in-table {\n    background: transparent;\n    border: 1px solid #BCBCBC !important;\n    border-radius: 4px !important;\n    height: 32px;\n    display: flex;\n    align-items: center;\n}\n.search-input {\n    background: transparent;\n    border: none;\n}\n.search-input:focus {\n    outline: none;\n}\n.search-in-table i {\n    color: #BCBCBC;\n    font-size: 20px;\n    padding: 0px 4px;\n}\n\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover:not(:last-child):not(:first-child) {\n    background-color: #EEEEEE;\n}\n.users-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.users-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.items-per-page {\n    background: #FFFFFF;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    width: 84px;\n    padding: 7px 12px;\n    margin-left: 12px\n}\n.filters {\n    width: 115px;\n    background: transparent;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 7px 12px;\n    margin-left: 20px;\n}\n.page-number {\n    padding: 8px 12px;\n    color: #616161;\n    font-size: 14px;\n    line-height: 16px;\n    background: none;\n    border: none\n}\n.page-number.active {\n    background: #151233;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.change-page-number {\n    border: none;\n    background: none;\n}\n.change-page-number i {\n    color: #151233; \n    font-size: 20px; \n    line-height: 20px;\n}\n.quick-filters {\n    display: flex;\n    align-items: center;\n    margin-left: 20px;\n}\n.quick-filters label {\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    color: #616161;\n    margin-right: 4px;\n}\n\n.quick-filters span {\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    padding: 8px 16px;\n    margin-right: 4px;\n    cursor: pointer;\n}\n.quick-filters span:hover {\n    background: #D5D5D5;\n    border-radius: 4px;\n}\n.quick-filters span.active {\n    background: #2C5A86;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.standalone-info {\n    font-size: 26px;\n}\n/* .standalone-info:hover + .button-tooltip {\n    visibility: visible;\n    opacity: 1;\n} */\n.button-tooltip {\n    /* visibility: hidden; */\n    position: absolute;\n    background: #1b4874;\n    color: #FFFFFF;\n    font-size: 12px;\n    font-family: 'Roboto';\n    text-align: left;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    /* top: 56px; */\n    /* left: 63px; */\n    /* opacity: 0; */\n    z-index: 1;\n    width: 200px;\n    max-height: 250px;\n    overflow: auto;\n}\n</style>\n<style>\n    .item-count,\n    .selected-filter {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page .items,\n    .filters .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option,\n    .filters .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover,\n    .filters .items .option:hover {\n        background-color: #EEEEEE;\n    }\n    .items-per-page i,\n    .filters i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
